import React, { useState } from 'react';import { Outlet, useLocation } from 'react-router-dom';import { CSSTransition, TransitionGroup } from 'react-transition-group';import { SideBar } from 'view/components/SIdeBar/side-bar';import MainHeader from '../../components/MainHeader/main-header';import styles from './Main.module.scss';export const Main = (): React.ReactElement => {	const location = useLocation();	const [toggleSideBar, setToggleSideBar] = useState<boolean>(false);	return (		<>			<MainHeader toggleSideBar={toggleSideBar} setToggleSideBar={setToggleSideBar} />			<SideBar toggleSideBar={toggleSideBar} setToggleSideBar={setToggleSideBar} />			<div className={styles['main']}>				<div className={styles['main_content']}>					<TransitionGroup>						<CSSTransition							timeout={{ enter: 750, exit: 150 }}							key={location.pathname}							classNames={'fade'}							unmountOnExit						>							<div className={styles['main_content__page']}>								<Outlet />							</div>						</CSSTransition>					</TransitionGroup>				</div>			</div>		</>	);};